import React from "react";
import "./css/ProductItem.css"
import {
    DeleteOutlined,
} from '@ant-design/icons';
import {Col, Popover, Divider, Row, Image} from "antd";
import {useDispatch} from "react-redux";
import {dispatchAction} from "../../services/utils/function.service";
import actions from "../../redux/product/actions";
import Text from "antd/es/typography/Text";
import * as productHelper from "../../helpers/product.utils"

function ProductItem({product, differentKeys}) {
    const dispatch = useDispatch()

    const deleteFromSelectedProduct = (productId) => {
        dispatch(dispatchAction(actions.DELETE_SELECT_PRODUCT, {productId: productId}))
    }

    const renderImage = () => {
        return (
            <Image
                style={{
                    maxHeight:100,
                    maxWidth:100,
                }}
                resizeMode="contain"
                src={product.productImage}
                fallback="data:image/png;base64,"
            />
        )
    }

    let keysSorted=productHelper.orderAlphabetacallyKey(product);
    console.log({keysSorted});

    return (
        <Col className="product_container " span={6}>
            <div className="spaceFlex flex_column bgwhite product_inside">
                <div className={'flex_row'} style={{padding: '9px'}}>
                    {renderImage()}
                    <DeleteOutlined className={"trash_app"}
                                    onClick={() => {
                                        deleteFromSelectedProduct(product.name)
                                    }}
                    />
                </div>
                <Text style={{
                    fontSize: 12,
                    color: 'blue',
                    paddingLeft: 3,
                }}>{product.name}
                </Text>
                <Divider/>

                <Row style={{
                    flexDirection: 'row',
                    width: '100%',
                    height: 20,
                    justifyContent: 'center'

                }}>
                    {product.badges.split('|').map(imageLink => (
                        <Image
                            src={imageLink}
                            width={20}
                            height={20}
                            style={{
                                margin: 1
                            }}
                        />))}
                </Row>
                <Divider/>

                {keysSorted.map(key  => {
                    let value=product[key];
                    switch (key) {
                        case ('name'):
                            return null;
                            break;
                        case ('atp'):
                            return null;
                            break;
                        case ('display'):
                            return null;
                            break;
                        case ('badges'):
                            return null;
                            break;
                        case ('productImage'):
                            return null;
                        default:
                            return (
                                <>
                                    {value !== "" &&
                                    <Col style={{
                                        marginTop: 10,
                                        flex: 1,
                                        backgroundColor: differentKeys.includes(key) ? '#EAEAEA' : "white"
                                    }}>
                                        <Popover title={key} content={null}>
                                            <Text
                                                key={key}>
                                                {value.toString()}
                                            </Text>
                                        </Popover>

                                    </Col>

                                    }
                                </>
                            )
                            break;
                    }

                })}


            </div>
        </Col>

    )
}

export default ProductItem
